.select-list-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 1.5rem;
  font-size: large;
}

@media (max-width: 500px) {
  .select-list-item {
    min-width: 100%;
  }
}

@media (min-width: 900px) {
  .select-list-item {
    font-size: x-large;
  }
}

.select-list-item,
.select-list-item img {
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.select-list-item img {
  margin: 0 0.75rem;
  width: 75px;
  height: 75px;
  border-radius: 15px;
}

.select-list-item a {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  position: relative;
  color: #333;
  text-decoration: none;
}

.select-list-item a:after {
  position: absolute;
  content: '';
  height: 3px;
  background: #d472bc;
  -webkit-transition: all 0.25s linear;
  -o-transition: all 0.25s linear;
  transition: all 0.25s linear;
  width: 0;
  left: 0px;
  bottom: -3px;
}

.select-list-item a:hover {
  border: 0;
}


