.select-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 1rem;
  margin-bottom: 0;
}

@media (min-width: 725px) {
  .select-list {
    margin-bottom: 1rem;
    -ms-flex: 0 0 75%;
        flex: 0 0 75%;
  }

  .select-list.has-selection {
    -ms-flex: 0 0 25%;
        flex: 0 0 25%;
  }
}

.select-list {
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.select-list:not(.has-selection) li {
  margin: 5%;
  -ms-flex: 0 0 25%;
      flex: 0 0 25%;
}

.select-list.has-selection li {
  -ms-flex: 0 0 100%;
      flex: 0 0 100%;
}

.select-list.has-selection li a:after {
  left: 100%;
}

.select-list li.selected a:after,
.select-list:not(.has-selection) li:hover a:after {
  width: 100%;
  left: 0;
}

.select-list.has-selection li img {
  width: 50px;
  height: 50px;
  border-radius: 5px;
}



