#work-and-internships-section  {
  background-color: white;
  text-align: center;
  border-bottom: 1px solid #ccc;
  z-index: 5;
}

@media (min-width: 500px) {
  #work-and-internships-section .cv-details .cv-detail {
    min-width: 400px;
  }
}

.work-history {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.work-image img {
  width: 100%;
}

.work-history-item a:hover {
  border-bottom: white;
}


