#about-section {
  z-index: -1;
  background-color: #2D3E50;
  color: #f0f0f0;
  font-size: 110%;
  text-align: center;
  border-bottom: 1px solid #ccc;
}

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .csspositionsticky #about-section {
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
  }
}


